h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  font-size: 1.5rem;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: var(--color-grey-dark);

  &:hover {
    color: var(--color-primary);
  }
}

.text-underline {
  text-decoration: underline;
  text-underline-position: under;
}

li {
  list-style: none;
}
